import React, { Fragment, useEffect, useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Row, Col, Input, Collapse, Button, notification } from 'antd';
import FormControl from 'components/UI/FormControl/FormControl';
import { FormTitle } from './AccountSettings.style';
import Loader from 'components/Loader/Loader';
import useServerApi from 'library/hooks/useServerApi';
import isEmpty from 'lodash/isEmpty';
import { setNftPrice, setPause } from 'web3/Premier';
import AxiosFetch from 'library/hooks/AxiosFetch';

const { Panel } = Collapse;

const AdminNFTSettings = (props) => {  
  const {metamaskConnected, account} = props;

  const [saleStatus, setSaleStatus] = useState();

  const fetchData = async () => {
    const url = '/getSaleStatus';    
    const res = await AxiosFetch(url);

    console.log(res);
    if (res.data.success) { 
      setSaleStatus(res.data.data);
    }
  }

  useEffect(fetchData, []);

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm();

  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const onSubmit = async (submitData) => {
    if(!metamaskConnected)
    {
      openNotificationWithIcon('error', 'Error!', "You need to connect metamask!");
      return;
    }
    const newPrice = submitData.price;
    await setNftPrice(newPrice, account).then(
      (data) => {        
        if (data.success == false) {
          if (data.type == "estimategas") {
            openNotificationWithIcon('error', 'Error!', `Insufficient funds to mint. Please consider some gas fee!`)              
          } else if (data.type == "setPrice") {
            openNotificationWithIcon('error', 'Error!', `Error happened while processing, please try again later`)                            
          }
        }
        else
        {
          handleSetPrice(newPrice)
        }
      }
    );      
  }

  const handleSetPrice = async(newPrice) => {
    let url = '/updateNFTPrice';
    const body = {
      price: newPrice
    }
    
    let res = await AxiosFetch(url, body, "POST");
    if(res.data.success)
      openNotificationWithIcon('success', 'Success', `You have changed NFT price successfully.`)
  }
  
  const handleSaleUpdate = async(status) => {    
    let newStatus;
    if(status == 'allow')
      newStatus = true;
    else
      newStatus = false;

    await setPause(!newStatus, account).then(
      (data) => {        
        if (data.success == false) {
          if (data.type == "estimategas") {
            openNotificationWithIcon('error', 'Error!', `Insufficient funds to mint. Please consider some gas fee!`)              
          } else if (data.type == "setPause") {
            openNotificationWithIcon('error', 'Error!', `Error happened while processing, please try again later`)                            
          }
        }
        else
        {
          handleSetSaleStatus(newStatus)
        }
      }
    );
  }

  const handleSetSaleStatus = async (newStatus) => {
    let url = '/updateSaleStatus';
    let pStatus = 1;
    if(newStatus)
      pStatus = 1;
    else
      pStatus = 0;
    const body = {
      status: pStatus
    }
    
    let res = await AxiosFetch(url, body, "POST");
    if(res.data.success) {
      openNotificationWithIcon('success', 'Success', `You have updated sale status successfully.`)
      setSaleStatus(newStatus);
    }
  }

  return (
    <Fragment>
      <FormTitle>NFT Settings</FormTitle>
      
      <Collapse defaultActiveKey={['1']}>
        <Panel header="NFT Price Setting" key="1">
          <form className="form-container" onSubmit={handleSubmit(onSubmit)}>
            <Row gutter={30}>
              <Col lg={24}>
                <FormControl
                  label="New Price"
                  htmlFor="price"
                  error={errors.price && <span>This field is required!</span>}
                >
                  <Controller
                    name="price"
                    control={control}
                    rules={{ required: true }}
                    render={({ field: { onChange, onBlur, value } }) => (
                      <Input onChange={onChange} onBlur={onBlur} value={ value } type="number" />
                    )}
                  />
                </FormControl>
              </Col>
            </Row>

            <div className="submit-container">
              <Button htmlType="submit" type="primary">
                Change Price
              </Button>
            </div>
          </form>        
        </Panel>
        <Panel header="Pause/Allow Minting" key="2">
          {!saleStatus && (
            <Button type="primary" onClick={() => handleSaleUpdate('allow')}>
              Allow
            </Button>
          )}
          {saleStatus && (
            <Button type="primary" danger onClick={() => handleSaleUpdate('pause')}>
              Pause
            </Button>
          )}          
        </Panel>        
      </Collapse>
    </Fragment>
  );
}

export default AdminNFTSettings;